<script lang="ts" setup>
import { GoToUserLogin, LoginUser, PageBack } from '@/commons/Global';
import BaseDialog from '@/components/BaseDialog.vue';
import { ref } from 'vue';
const logoutedVisable = ref(false)
const nologinVisable = ref(false)
</script>

<template>
    <BaseDialog -body="退出登录成功" v-model="logoutedVisable" :-close-action="() => {
        PageBack()
    }" />
    <BaseDialog -body="你还未登录" v-model="nologinVisable" :-close-action="GoToUserLogin" />
    <div v-if="LoginUser !== null" style="display: none;" v-once>
        {{ logoutedVisable = true }}
    </div>
    <div v-else style="display: none;" v-once>
        {{ nologinVisable = true }}
    </div>
</template>